body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container{
  width: 100%;
  height: 100%;

}
@media screen and (min-width: 540px) {
  .container {
    width: 600px !important;
  }
}

.box {
  display: flex !important;
}

.song-title {
  margin-left: 20px;
}

.controls {
  display: flex !important;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.controls .button {
  margin: 5px;
}

.current-track {
  width: 80%;
}
.footx {
  background-color: rgb(238, 236, 236);
  padding-top: 10px;
  padding-bottom: 3px;
}

.row-header {
  margin: 0px auto;
  padding: 0px auto;
}

.high {
  text-align: center;
  text-transform: capitalize;
  background-color: rgb(241, 216, 185);
  font-family: Georgia, "Times New Roman", Times, serif;
}

.row-content {
  margin: 0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height: 400px;
}

.footer {
  background-color: #c8d3d3;
  margin: 0px auto;
  padding: 20px 0px 20px 0px;
}
.jumbotron {
  padding: 40px 30px 30px 30px;
  margin: 0px auto;
  background: #e4e4e4;
  color: rgb(87, 58, 0);
}
.custom {
  padding-left: 340px;
  padding-top: 20px;
}
.pad {
  padding-top: 20px;
}
.paded {
  padding-top: 20px;
  padding-left: 10px;
  margin-left: 100px;
}

address {
  font-size: 80%;
  margin: 0px;
  color: #a15b00;
}

.navbar-dark {
  background-color: #c01355;
}

.page-enter {
  opacity: 0.01;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.page-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 300ms ease-in;
}

.page-exit {
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.page-exit-active {
  opacity: 0.01;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: all 300ms ease-out;
}

.bgimage {
  background-color: black;
  image-orientation: initial;
}
.all {
  background-color: rgb(243, 243, 243);
}

.alldark {
  background-color: rgb(255, 255, 255);
}
.allmenu {
  background-color: rgb(14, 11, 11);
}
.feedback {
  background-color: whitesmoke;
}

.navigationButtons {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
  margin: auto;
  padding-top: 40px;
}

.navigationButtons a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid red;
  color: red;
  cursor: pointer;
}

.navigationButtons a:hover {
  color: white;
  background-color: red;
}

.navigationActive a {
  color: white;
  background-color: red;
}

.navigationDisabled a {
  color: grey;
  background-color: grey;
}
.card {
  width: 40%;
  height: auto;
  background-color: rgb(187, 127, 71);
  margin: auto;
  border: 2px solid black;
}

.home {
  padding-top: 100px;
  height: auto;
  border: #c9c5c1;
  border-radius: 1px;
  border-style: inherit;
}



footer {
  padding: 2px 0;
  background-color:rgb(0, 0, 0);
  text-align: center;
}
footer h1 {
  padding-top: 6px;
  font-size: 20px;
  color: #ffffff;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 0px;
}
footer p {
  color: #ffffff;
  font-size: 14px;
  margin-bottom: 10px;
}
footer ul.social-list li {
  display: inline-block;
  margin: 0 8px;
}
footer ul.social-list li a {
  color: #666666;
  font-size: 16px;
}
footer ul.social-list li a:hover {
  color: #e74c3c;
}
footer.dark-style {
  background-color: #333333;
}
footer.dark-style h1 {
  color: #fff;
}


.pagination {
  display: flex;
  width: 135%;
  height: 60px;
  padding-right: 30%;
  padding-top: 10px;
  justify-content:center;
  list-style: none;
  cursor: pointer;
  color: rgb(255, 255, 255);
}

.pagination a {
  padding: 10px;
  margin: 1px;
  border-radius: 10px;
  border: 2px solid #ffffff;
  color: #ffffff;
}

.pagination__link {
  font-weight: bold;
}

.pagination__link--active a {
  color: #fff;
  background: #009225;
}

.pagination__link--disabled a {
  color: rgb(198, 197, 202);
  border: 1px solid rgb(198, 197, 202);
}

.pagination a:hover {
  color: white;
  background-color:rgb(97, 221, 255);
}
.cardtop {
  width: 100%;
  height: 100%;
  margin: auto;
  min-height: 800px;
  box-shadow: 0px 35px 70px 0px rgba(255, 163, 125, 0.65), 0px 30px 40px -10px rgba(255, 100, 124, 0.55);
  background: linear-gradient(180deg, rgba(255, 163, 125, 1) 0%, rgba(255, 99, 124, 1) 100%);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 540px) {
  .card {
    width: 100%;
    height: 100%;
    border-radius: 0;
    box-shadow: none;
  }
}

.header {
  display: flex;
  padding: 10px;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

.logo {
  font-weight: bold;
  font-size: 1.5em;
  transition: opacity 0.05s ease;
  padding-left: 20%;
}

.logo:hover {
  opacity: 0.75;
}

.content {
  flex: 0.5 0.5 auto;
  min-height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.content .title-holder {
  color: #fff;
  text-align: center;
  margin-bottom: 29px;
}

.content .title-holder h1 {
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 12px;
}

.content .title-holder p {
  font-size: 16px;
  line-height: 28px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.75);
}

@media screen and (max-width: 768px) {
  .content .title-holder {
    max-width: 80%;
  }
}

.content .cta {
  min-width: 64px;
  padding: 16px 20px;
  background: #fff;
  border-radius: 50px;
  cursor: pointer;
  text-align: center;
  font-size: 1em;
  font-weight: bold;
  -webkit-transform: none;
          transform: none;
  box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.25, 0.315, 1.35), -webkit-transform 0.1s linear;
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.25, 0.315, 1.35), transform 0.1s linear;
  transition: box-shadow 0.3s cubic-bezier(0.25, 0.25, 0.315, 1.35), transform 0.1s linear, -webkit-transform 0.1s linear;
}

.content .cta:hover {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.3);
}

.footer {
  display: flex;
  flex-direction: row;
  padding: 48px;
  justify-content: center;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
}

a.underlined:hover {
  border-bottom: 1px dotted #fff;
}

